import Vue from 'vue'
import Router from 'vue-router'
import Login from '../components/page/login/Login' //
import Register from '../components/page/register/Register' //
import Index from '../components/page/Index'  //index 

import NotFoundComponent from '../components/page/NotFoundComponent' //404

import UserManagement from '../components/page/system_management/User_management'
import MTt from '../components/page/myTimetable/MyTimetable' 

import SmallSchool from '../components/page/smallSchool/SmallSchool' // 
import SchoolDetail from '../components/page/smallSchool/SchoolDetail' //
import NewsInformation from '../components/page/smallSchool/NewsInformation' //

import ExquisiteList from '../components/page/exquisiteClassroom/ExquisiteList' //
import CoursePlayback from '../components/page/exquisiteClassroom/CoursePlayback' //
import RecordingPlayback from '../components/page/exquisiteClassroom/RecordingPlayback' //


Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    { path: '*', component: NotFoundComponent },

    {
      path: '/',
      name: 'Index',
      component: Index
    },
    {
      path: '/login',
      name: 'login',
      component: Login
    },
    {
      path: '/register',
      name: 'register',
      component: Register
    },
    
    {
      path: '/myTimetable',
      name: 'MTt',
      component: MTt
    },

    {
      path: '/smallSchool',
      name: 'smallSchool',
      component: SmallSchool
      
    },
    { path: '/schoolDetail/:id',
      name:'schoolDetail',
      component: SchoolDetail 
    },
    { path: '/newsInformation/:id',
      name:'newsInformation',
      component:NewsInformation
     
    },
     {
      path: '/exquisiteList',
      name: 'exquisiteList',
      component: ExquisiteList
      
    },
    {
      path: '/coursePlayback/:id',
      name: 'coursePlayback',
      component: CoursePlayback
      
    },

    { path: '/recordingPlayback/:id',
      name:'recordingPlayback',
      component: RecordingPlayback
    }


  ]
})



